class SquareData{
  constructor(x, y, type) {
    this.x = x;
    this.y = y;
    this.type = type; //0 = NORMAL, 1 = LETTER X2, 2 = LETTER X3, 3 = LETTER X4, 4 = WORD X2, 5 = WORD X3, 6 = WORD X4, 5 = START
    this.selected = false;
    this.letter = null;
    this.tempLetter = null;
    this.isSelected = false;
  }
}
class Player{
  constructor(name, id, androidId) {
    this.name = name;
    this.id = id;
    this.androidId = androidId;
    this.score = 0;
    this.letters = new Array();
    this.turns = new Array();
    this.clientIndex = 0;
  }
}

class Letter{
  constructor(value, score) {
    this.value = value;
    this.score = score;
  }
}



